// import initSqlJs from 'sql.js';

//  FunctionHelper  WindowHelper
// FunctionHelper 
// WindowHelper 
// import FunctionHelper from './FunctionHelper';
// import WindowHelper from './WindowHelper';

let SQLiteDatabaseHelper = {
  db: null, // SQLite 
  SQL: null, // sql.js 
  quota: 30 * 1024 * 1024 /*5MB*/,
  filePath: 'database.sqlite',
  debug: false, // 
  
  // 
  init: async function (callback) {
    // 
    if (this.db === null) {
      try {
        await navigator.webkitPersistentStorage.requestQuota(this.quota)
        window.requestFileSystem = window.requestFileSystem || window.webkitRequestFileSystem;

        let savedDB
        try {
          savedDB = await this.readSavedDB();
        }
        catch (e) {
          if (this.debug) {
            console.error(' :', e);
            console.log(' ');
          }
        }

        let SQL = await this.initSQL(); //  SQL 
        if (savedDB) {
          //  localStorage 
          // const uInt8Array = Uint8Array.from(atob(savedDB), c => c.charCodeAt(0));
          // db = new SQL.Database(uInt8Array);
          this.db = new SQL.Database(savedDB);
          if (this.debug) {
            console.log(' ');
          }
        } else {
          // 
          this.db = new SQL.Database();
          if (this.debug) {
            console.log(' ');
          }
        }

        if (this.debug) {
          console.log(' :', this.db);
        }
      } catch (e) {
        console.error('Failed to initialize SQLite database:', e);
        WindowHelper.errorHandler(e); //  WindowHelper 
        return;
      }
    }
    // 
    FunctionHelper.triggerCallback(callback);
  },
  initSQL: async function () {
    return new Promise((resolve, reject) => {
      if (this.SQL) {
        resolve(this.SQL);
      } else {
        const config = {
          // locateFile: filename => `https://unpkg.com/sql.js@latest/dist/${filename}`
          locateFile: filename => `static/sql.js/sql-wasm.wasm`
        };
        //  SQL  sql.js 
        initSqlJs(config).then((SQL) => {
          this.SQL = SQL;
          resolve(SQL);
        }).catch((error) => {
          console.error('Failed to load sql.js:', error);
          reject(error);
        });
      }
    });
  },
  readSavedDB: function () {
      return new Promise((resolve, reject) => {
        window.requestFileSystem(window.PERSISTENT, this.quota, (fs) => {
          fs.root.getFile(this.filePath, {}, (fileEntry) => {
            fileEntry.file((file) => {
              const reader = new FileReader();
              reader.onloadend = () => {
                //  ArrayBuffer  Uint8Array
                if (this.debug) {
                  console.log(' :', fileEntry.name);
                }
                resolve(new Uint8Array(reader.result));
              };
              reader.onerror = (error) => {
                reject(error);
              };
              reader.readAsArrayBuffer(file);
            }, (error) => {
              reject(error);
            });
          }, (error) => {
            reject(error);
          });
        }, (error) => {
          reject(error);
        });
      });
  },

  //  SQL 
  exec: async function (sql, dataArray, callback) {
    if (this.debug) {
      console.log('[EXEC] ' + sql); //  SQL 
    }
    //  dataArray 
    if (typeof(dataArray) === 'function') {
      callback = dataArray;
      dataArray = [];
    }
    
    // 
    await this.init(); // 

    try {
      let output;
      //  SELECT 
      if (sql.trim().toUpperCase().startsWith('SELECT')) {
        // 
        const res = this.db.exec(sql, dataArray); //  db.exec 
        if (res.length > 0) {
          //  WebSQL  ()
          output = res[0].values.map(row => {
            let obj = {};
            res[0].columns.forEach((col, idx) => {
              obj[col] = row[idx];
            });
            return obj;
          });
        } else {
          output = [];
        }
      } else {
        //  SELECT  (INSERT, UPDATE, DELETE)
        this.db.run(sql, dataArray); //  db.run 
        //  INSERT  ID
        if (sql.trim().toUpperCase().startsWith('INSERT')) {
          const res = this.db.exec('SELECT last_insert_rowid() AS last_id'); //  ID
          if (res.length > 0 && res[0].values.length > 0) {
            output = res[0].values[0][0]; //  last_insert_rowid 
          } else {
            output = null; //  ID null
          }
        } else {
          output = null; //  UPDATE/DELETE
        }
      }
      await this.writeSavedDB(); // 
      
      // 
      FunctionHelper.triggerCallback(callback, output);
    } catch (e) {
      //  SQL 
      this.errorHandler(e, sql); //  errorHandler 
    }
  },
  writeSavedDB: function () {
      return new Promise((resolve, reject) => {
        window.requestFileSystem(window.PERSISTENT, this.quota, (fs) => {
          // localStorage.setItem('myDatabase', base64String);
          fs.root.getFile(this.filePath, {
            create: true, exclusive: false
          }, (fileEntry) => {
            fileEntry.createWriter((fileWriter) => {
              // console.log(binaryArray)
              const binaryArray = this.db.export();
              const blob = new Blob([binaryArray], { type: 'application/octet-stream' });
              fileWriter.write(blob);
              if (this.debug) {
                console.log(' :', this.filePath);
              }
              
            }, (error) => {
              reject(' :', error);
            });
          }, (error) => {
            reject(' :', error);
          });

          if (this.debug) {
            console.log('  FileSystem');
          }
          resolve(true); //  Promisep
        }, (error) => {
          reject(error);
        });
      });
  },

  // 
  errorHandler: function (e, sql) {
    WindowHelper.errorHandler(e); //  WindowHelper 
    console.trace(`SQLiteDatabaseHelper error: ${sql}`); // 
    console.log(e); // 
  }
}

//  SQLiteDatabaseHelper  window 
// window.SQLiteDatabaseHelper = SQLiteDatabaseHelper;

// Alias
window.WebSQLDatabaseHelper = SQLiteDatabaseHelper;
//  SQLiteDatabaseHelper
export default SQLiteDatabaseHelper;
